@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap");

* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}
/* css variable declerarion */
:root {
  --color-bg: rgb(177, 60, 147);
  --color-bg-variant: rgba(56, 17, 173, 0.637);
  --color-primary: rgba(255, 230, 8, 0.733);
  --color-primary-variant: rgba(255, 255, 255, 0.452);
  --color-white: rgb(219, 180, 106);
  --color-light: rgba(255, 255, 255, 0.6);
  --color-hilight: rgb(10, 208, 238);
  --color-lolight: rgb(57, 4, 100);

  --transition: all 400ms ease;

  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;
  --toastify-color-success:  rgb(108, 205, 185);
  --toastify-icon-color-success: var(--toastify-color-success);
}

html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  display: none;
}

body {
  font-family: "Poppins", sans-serif;
  background-color: var(--color-bg);
  color: var(--color-white);
  line-height: 1.7;
  background-image: url(../src/assets/backdrop.png);
  background-size: 15rem;
}

/* ---------------- GENERAL STYLES ---------------- */

.container {
  width: var(--container-width-lg);
  margin: 0 auto;
}

h1,
h2,
h3 {
  font-weight: 800;

}

h2 {
  font-size: 2.5rem;
}

h1 {
  font-size: 3rem;
}

section {
  margin-top: 2rem;
  height: 100%;
}

section > h2,
section > h5 {
  text-align: center;
  color: var(--color-light);
}

section > h2 {
  color: var(--color-primary);
  margin-bottom: 1rem;

}

.text-light {
  color: var(--color-light);
}

a {
  color: var(--color-primary);
  transition: var(--transition);
}

a:hover {
  color: var(--color-white);
}

.btn {

  height: 3rem;
  display: inline-block;
  color: var(--color-primary);
  padding: 0.7rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid var(--color-primary);
  transition: var(--transition);
  align-items: center;
}

.btn:hover {
  background: var(--color-white);
  color: var(--color-bg);
  border-color: transparent;
}

.btn-primary {
  background: var(--color-primary);
  color: var(--color-bg);
}

img {
  display: block;
  width: 100%;
  object-fit: cover;
}

/* ---------------- Media Queries (Med Devices) ---------------- */
@media screen and (max-width: 1024) {
  .container {
    width: var(--container-width-md);
  }

  section {
    margin-top: 6rem;
  }
}

/* ---------------- Media Queries (Small Devices) ---------------- */
@media screen and (max-width: 600) {
  .container {
    width: var(--container-width-sm);
  }

  section > h2 {
    margin-bottom: 1rem;
  }
}
