.about__container {
  display: grid;
  grid-template-columns: 1fr 2fr;
  gap: 2rem;
  justify-content: center;
  width: 100%;
  place-items: center;
  padding-top: 1rem;
  /* background-image: url('../../assets/Header.png');
  background-size: cover;

  background-repeat: no-repeat; */
}

.reveal__button {
  font-family: "Poppins", sans-serif;
  background-color: var(--color-bg);
  color: var(--color-white);
  line-height: 1.7;
  background-size: 15rem;
  font-size: medium;
  padding: 0 10px;
}

.about__me {
  border-radius: 50%;
  width: 250px;
  height: 250px;
  background: var(--color-primary);
  display: flex;
  z-index: 1;
  justify-content: center;
  align-items: center;
}

.about__me-image {
  justify-content: center;
  width: 90%;
  align-self: center;
}

.about__me-cartoon {
  justify-content: center;
  width: 90%;
}
